#root {
  display: flex;
  flex-flow: column wrap;
}

.deck {
  display: flex;
  flex-flow: row wrap;
}

.card {
  display: flex;
  width: 48px;
  height: 80px;
  background-color: #FFF;
  border-radius: $base-size;
  border: 1px solid black;
  margin: 16px;
  padding: 4px;
}

.card-black {
  color: black;
}

.card-red {
  color: red;
}

.card-value {
  font-size: 16px;
}

.card-suit {
  font-size: 8px;
}

.card-tl {
  display: flex;
  flex-grow: 1;
  flex-flow: column nowrap;
  align-items: flex-start;
}

.card-br {
  display: flex;
  flex-grow: 1;
  flex-flow: column nowrap;
  justify-content: flex-start;
  transform:rotate(-180deg);
}

input[type=button] {
  padding: 8px 16px;
  margin: 16px;
  border-radius: 8px;
  background-color: red;
  border: 1px solid white;
  color: white;
  text-transform: uppercase;
}

.pattern {
  border: 1px solid;
  border-collapse: collapse;
  width: 200px;
  height: 200px;
  margin: 10px auto;
  text-align: center;
}

.button {
  margin: 10px auto;
}

.button-div {
  width: 100%;
  text-align: center;
}

.left-panel {
  width: 20%;
  height: 100px;
  float: left;
}

.right-panel {
  margin-left: 20%;
  height: 100px;
}

.dot {
  height: 25px;
  width: 25px;
  background-color: #FF0000;
  border-radius: 50%;
  display: inline-block;
}